.blogList {
  display: flex;
  flex-direction: column;
  background: white;
  margin: 0px 20%;
  border-radius: 10px;
  box-lines: 5px;
  box-shadow: 1px 1px black;
}
.blogItem {
  font-size: 1.5em;
  border-top: gray solid 1px;
  padding: 15px 0;
  text-align: center;
}
.blogItem .link {
  color: #ec407a;
  text-decoration: none;
  font-weight: 700;
}
.blogItem .link:hover {
  color: purple;
}